<template>
<div>
<el-form style="margin-top:20px;">
<el-row style="margin-left:20px;">
    <el-col :span="12">
        <el-form-item label="请输入筛选条件：">
            <el-input v-model="input_string" placeholder="输入筛选条件" style="width: 420px;">
            </el-input>
        </el-form-item>
    </el-col>
    <el-col :span="8" style="text-align: right;padding-right:50px;">
        <el-button-group>
            <el-button type="primary" icon="el-icon-search" @click="querykaoqing()">筛选</el-button>
            <el-button type="primary" icon="el-icon-tickets" @click="getkaoqing()">全部</el-button>
        </el-button-group>
    </el-col>
</el-row>
</el-form>
<el-table :data="tableData"  border style="width: 50%;margin-left:20px" size="mini">
        <el-table-column prop="param2__id_curricula__name" label="签到课程名称" width="180" >
        </el-table-column>
        <el-table-column prop="param2__timebegin" label="开始时间" width="180" >
        </el-table-column>
        <el-table-column prop="param2__timeend" label="结束时间" width="180">
        </el-table-column>
        <el-table-column prop="attendrates" label="签到" width="180">
        </el-table-column>
</el-table>
<el-row  style="text-align: left;margin-left:160px">
    <el-pagination         
	      @size-change="handleSizeChange"
	      @current-change="handleCurrentChange"
	      :current-page.sync="currentpage"
        :page-size="pagesize"
	      layout="total,prev, pager, next, jumper"
	      :total="total">       
    </el-pagination>
</el-row>
</div>
</template>

<style>
  .el-table .warning-row {
    background: rgb(212, 58, 31);
  }

  .el-table .success-row {
    background: #5aee0b;
  }
</style>

<script>
import axios from 'axios';
  export default {
    data() {
      return {
      baseURL:"http://localhost:9000/polls/",
      id:'',
      tableData:[],
      input_string:'', //输入的筛选条件
      total:0,
      currentpage:1,  //默认第一页
      pagesize:10, //默认展示10条数据
      limits:10,
      }
    },
    mounted(){
    //自动加载数据
    this.getkaoqing();
    },
    methods:{
      getkaoqing(){
        this.id = this.$route.query.index;
         if(!this.$route.query.index){
           this.$router.push({path:'kechengs/'})
         }
        console.log(this.id);
        let that = this;
        axios
        .get(
          that.baseURL + 'StudentAttendance/',{params:{course_plan_id:that.id,page:that.currentpage,limits:that.limits}})
        .then(function(res){
          if(res.data.code == 0){
              that.$router.push('/index');
          }else{
              if(res.data.error_code == 0){
                    that.tableData = res.data.data;
                    that.$message({
                        message:'数据加载成功！',
                        type:'success'
                    });
                }else {
                    //数据加载失败提示
                    that.$message.error(res.data.message);
                }
          }
          })
        .catch(function(err){
        that.$message.error("加载后端数据失败！");
        })
      },
      //实现当前页的设备信息筛选
        querykaoqing(){
            //使用Ajax请求--POST-->传递input_string
            let that = this;
            //开始Ajax请求
            axios
            .post(
                that.baseURL + 'StudentAttendance/',
                {
                    input_string: that.input_string
                }
            )
            .then(function(res){
                if(res.data.error_code == 0){
                    //把数据给shebei
                        that.tableData = res.data.data;
                    //获取返回记录的总行数
                        that.total = res.data.total_number*that.limits;
                    //数据加载成功提示
                    that.$message({
                        message:'筛选数据加载成功！',
                        type:'success'
                    });
                }else {
                    //数据加载失败提示
                    that.$message.error(res.data.message);
                }
            })
            .catch(function(err){
                //console.log(err);
                that.$message.error("筛选信息失败！");
            })
        },
        //分页时修改每页的行数
        handleSizeChange(size){
             //修改当前每页数据行数
             this.pagesize = size;
            //this.currentpage = 1;
             //数据重新分页
             this.getkaoqing();
         },
        //调整当前的页码
        handleCurrentChange(pageNumber){
            //修改当前的页码
            this.currentpage = pageNumber;
            //数据重新分页
            this.getkaoqing();
        },
    }
  }
</script>



